.navigation{
    width: 90%;
    /* max-width: 1000px; */
    /* margin: 20px auto; */
    background: #024959;
    border-radius: 10px;
    position:fixed;
    z-index: 100;
    margin: 0 6%;

}

ul>li{
    float: left;
    padding: 8px;
}

ul>li>a{
    padding: 20px;
    display:block;
    
}
.nav-items{
    width: 100%;
    margin: 20px auto;
    display: flex;
    padding: 3% 16%;
    /* padding-left: 211px; */

}
.control-menu{
    display:none;
}

ul.nav-items li:hover{
    background: #2980b9;
    position: relative;
    top: -5px;
    border-radius: 10px;
    cursor:pointer;
   
}
.menu_bar{
    display: none;
}
ul >li>span{
    margin-right: 10px;
}

@media screen and (max-width: 800px) {
   
    .navigation{
        width:100%;
        height: 100%;
        margin: 0;
        position: relative;
        display:block;
        
    }
    
    .nav-items{
        display: inline-table;
        margin: 0px;
        padding: 0px;
       
    }
    div.navigation ul li{
        display:flex;
        padding-left:5%; 
        padding-right:5%;
        float: none;
        border-bottom: 1px solid rgba(255,255,255, .3);
        padding: 20px;
        margin: 0;
    }
   
    .menu_bar{
        display: block;
        width: 100%;

    }

    .contenedor,.footer,.contenedorcards{
        position: relative;
    }

    .menu_bar .btn-menu{
        display: block;
        padding: 20px;
        background: #024059;
        color: #fff;
        font-family:none;
        font-size:21px;
        font-weight: bold;
        box-sizing: border-box;
        text-align: end;
        padding-right: 55px;

    }
    
    
}   

